import React, { useState, useEffect } from "react";
import Card from "../Gists/Card";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import SimpleBreadcrumbs from "../../layout/SimpleBreadcrumbs";
import Skeleton from "@material-ui/lab/Skeleton";
import Box from "@material-ui/core/Box";
import ArrowBackIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIcon from "@material-ui/icons/ArrowForwardIos";
import IconButton from "@material-ui/core/IconButton";
import Hidden from "@material-ui/core/Hidden";
import Footer from "../../layout/Footer";
import { getHeaderBackgroundColor } from "../../utils/Theme";
import { useTheme } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";

//Assets
import bash640x425 from "../../assets/img/bash640x425.jpg";
import defaultIMG from "../../assets/img/7.png";
import Particles from "react-particles-js";

const useStyles = makeStyles((theme) => ({
  root: {
    width: 300,
  },
  container: {
    display: "flex",
    flexDirection: "column",
    [theme.breakpoints.between("xs", "md")]: {
      minHeight: "calc(100vh - 131px)",
    },
    [theme.breakpoints.up("md")]: {
      minHeight: "calc(100vh - 225px)",
    },
  },
}));

function Gists() {
  const classes = useStyles();
  const theme = useTheme();
  const [gists, setGists] = useState([]);
  const [page, setPage] = useState(1);

  //console.log('useLocation')
  //console.log(useLocation())

  useEffect(() => {
    document.title = "Гисты";
    if (gists instanceof Array && gists.length === 0) {
      loadGistsPage().then((firstPage) => setGists(firstPage));
    }
  }, []);

  useEffect(() => {
    if (page >= 1) {
      loadGistsPage().then((nextPage) => setGists(nextPage));
    }
  }, [page]);

  const loadGistsPage = async () => {
    try {
      const response = await fetch(
        `https://api.github.com/users/shandyrov/gists?page=${page}&per_page=9`
      );
      const contentType = response.headers.get("content-type");
      if (!contentType || !contentType.includes("application/json")) {
        throw new TypeError("Error: Content type is not  JSON!");
      }

      const json = await response.json();
      //console.log(`===LOAD GISTS PAGE: ${page} FROM GITHUB API===`)
      //console.log(json)

      return json;
    } catch (error) {
      console.error(error);
    }
  };

  const getCardsOrSkeletons = () => {
    return gists !== null ? getCards() : getSkeletons();
  };

  const getCards = () => {
    if (gists !== null && gists instanceof Array && gists.length > 0) {
      const i = gists.map((gist, index) => {
        return card(index, gist);
      });
      return i;
    } else {
      return getSkeletons();
    }
  };

  const card = (index, gist) => {
    const img = getCategoryImage(gist.files, gist.description);
    return (
      <Grid key={gist.id} item xs={12} sm={6} md={4}>
        <Card
          gistId={gist.id}
          image={img}
          category="gists"
          description={gist.description}
          content={gist.files.content}
          pageNumber={page}
        />
      </Grid>
    );
  };
  const getSkeletons = () => {
    let items = [];
    for (let i = 0; i < 9; i++) {
      items[i] = (
        <Grid key={i} item xs={12} sm={4} style={{ maxWidth: 345 }}>
          <Box style={{ width: "100%" }} marginRight={0.5} my={1}>
            <Skeleton variant="rect" style={{ width: "100%" }} height={170} />
            <Box>
              <Skeleton />
              <Skeleton width="60%" />
            </Box>
          </Box>
        </Grid>
      );
    }
    return items;
  };
  const getCategoryImage = (object1, object2) => {
    for (let [key, value] of Object.entries(object1)) {
      switch (value.language) {
        case "Shell":
          return bash640x425;
        case "Text":
          return bash640x425;
        default:
          return defaultIMG;
      }
    }
  };
  return (
    <React.Fragment>
      <Hidden only={["xs", "sm"]}>
        <Particles
          height={150}
          style={{ backgroundColor: getHeaderBackgroundColor(theme) }}
        />
      </Hidden>

      <Container maxWidth="md" className={classes.container}>
        <SimpleBreadcrumbs
          links={[{ title: "Главная", link: "/" }]}
          current="Гисты"
        />
        <br />
        <main>
          <Grid container spacing={2} style={{ marginBottom: "10px" }}>
            {getCardsOrSkeletons()}
          </Grid>
          <Grid
            container
            spacing={0}
            direction="column"
            alignItems="center"
            justify="center"
            style={{ paddingBottom: "20px" }}
          >
            <Grid item sm={12}>
              <IconButton
                onClick={(e) => setPage((page) => page - 1)}
                disabled={
                  gists === null ||
                  gists === undefined ||
                  gists.length === 0 ||
                  page === 1
                }
              >
                <ArrowBackIcon />
              </IconButton>
              {page}
              <IconButton
                onClick={(e) => setPage((page) => page + 1)}
                disabled={
                  (gists && gists.length === 0) || (gists && gists.length < 9)
                }
              >
                <ArrowForwardIcon />
              </IconButton>
            </Grid>
          </Grid>
        </main>
      </Container>
      <Footer />
    </React.Fragment>
  );
}
export default Gists;
